import React, { useState, useEffect } from 'react';
import Chart from 'react-apexcharts';
import moment from 'moment';

const defaultOptions: any = {
  chart: {
    toolbar: {
      show: false,
    },
  },
  title: {
    text: '',
  },
  xaxis: {
    type: 'datetime',
    labels: {
      formatter: (v: string) => moment(v).local().format('MM-DD'),
      rotate: 0,
    },
    tickAmount: 5,
    axisTicks: {
      show: false,
    },
  },
  yaxis: [
    {
      // Y  1: Price
      tooltip: {
        enabled: false,
      },
      labels: {
        show: false,
      },
    },
    {
      // Y  2: Volume
      opposite: true,
      labels: {
        show: false, //  Y 
      },
    },
  ],
  plotOptions: {
    candlestick: {
      colors: {
        upward: '#55b07c',
        downward: '#df4f55',
      },
      wick: {
        useFillColor: true, // 
      },
    },
  },
  colors: ['#FAB12F'], // 
  stroke: {
    width: [1, 0], // K
  },
  dataLabels: {
    enabled: false,
  },
  legend: {
    show: false, // 
  },
};

interface KlineChartProps {
  klineData: string[][];
}

export default function KlineChart(props: KlineChartProps) {
  const { klineData = [] } = props;
  const [chartData, setChartData] = useState<any[]>([]);
  const [volumeData, setVolumeData] = useState<any[]>([]);
  const [priceRange, setPriceRange] = useState<{ min: number; max: number }>({
    min: 0,
    max: 0,
  });

  useEffect(() => {
    const updatedChartData = klineData.map((k) => ({
      x: new Date(k[0]),
      y: [Number(k[1]), Number(k[2]), Number(k[3]), Number(k[4])],
    }));
    setChartData(updatedChartData);

    const updatedVolumeData = klineData.map((k) => ({
      x: new Date(k[0]),
      y: Number(k[5]), // k[5] 
    }));
    setVolumeData(updatedVolumeData);

    const prices = klineData.flatMap((k) => [
      Number(k[1]), // 
      Number(k[2]), // 
      Number(k[3]), // 
      Number(k[4]), // 
    ]);

    const minPrice = Math.min(...prices);
    const maxPrice = Math.max(...prices);
    setPriceRange({ min: minPrice, max: maxPrice });
  }, [klineData]); //  klineData 

  return (
    <Chart
      type="candlestick"
      height={200}
      options={{
        ...defaultOptions,
        yaxis: [
          {
            ...defaultOptions.yaxis[0],
            min: priceRange.min, // 
            max: priceRange.max, // 
            labels: {
              show: false, //  Y 
            },
          },
          {
            ...defaultOptions.yaxis[1],
            labels: {
              show: false, //  Y 
            },
          },
        ],
      }}
      series={[
        {
          name: 'Kline',
          type: 'candlestick',
          data: chartData,
        },
        {
          name: 'Volume',
          type: 'bar',
          data: volumeData,
        },
      ]}
    />
  );
}
